import ApiService from "./api.service";
class documentService {
  getAll(data: object) {
    return ApiService.post("getAllDocument", data);
  }

  addEdit(data: object) {
    return ApiService.post("addEditDocument", data);
  }

  delete(data: object) {
    return ApiService.post("deleteDocument", data);
  }

  uploadDocument(data: any) {
    return ApiService.post("uploadDocument", data);
  }

  getDataByTypeUrl(data: any) {
    return ApiService.post("getDocumentByTypeUrl", data);
  }

  getFile(data: any) {
    return ApiService.post("getFile", data, { responseType: "blob" });
  }
}
export default new documentService();
